.menu{
  position: relative;
}
.icon{
  position: absolute;
  width: 26px;
  height: 20px;
  left: 17px;
  top: 42px;
}
.menu-content{
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
  opacity: 1;
  -webkit-transition: opacity .3s;
  -o-transition: opacity .3s;
  transition: opacity .3s;
  display: none;
}
.left-wrap{
  position: fixed;
  z-index: 2;
  width: 300px;
  margin-left: -300px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;

}
.left{
  background-color: #fff;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: auto;
  padding: 2.5em 1.5em;
  font-size: 1.15em;
  height: 100vh;
}
.left-item{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
}
.left-user{
  background-image: url('https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3884012340,3727116094&fm=27&gp=0.jpg');
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
  background-size: 170px;
  background-position: center center;
}
.left-list{
  -ms-flex-positive: 2;
      flex-grow: 2;
}
.list-button{
  display: block;
  line-height: 56px;
  border-bottom: 1px solid #E3E9EC;
  padding-left: 10px;
  font-size: 12px;
  color: #878787;
  text-decoration: none;
}
.bottom-button{
  display: block;
  outline: none;
}
.bottom-button>button{
  display: block;
  color: #fff;
  background-image:-webkit-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image:-o-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image:linear-gradient(-45deg, #F77062 0%, #FE5196 100%);
  border-radius: 2px;
  font-size: 14px;
  border: 0;
  width: 80%;
  margin: 30px auto;
  line-height: 40px;
}
.show{
  display: block;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}



